$input-bg-color: rgba($black, .025);
$input-bg-focus-color: rgba($black, .0625);
$input-bg-hover-color: rgba($black, .05);
$input-radio-bg-color: rgba($black, .075);

$input-bg-error-color: rgba($red, .125);
$input-bg-error-focus-color: rgba($red, .25);
$input-bg-error-hover-color: rgba($red, .25);

$input-border-color: rgba($black, .125);
$input-border-focus-color: rgba($black, .25);
$input-selected-border-color: rgba($black, .5);

.forminator-title {
    font-size: 1.25rem;
    letter-spacing: 0;
    margin-bottom: $paragraph-margin-bottom / 4;

    @include media-breakpoint-up(md) {
        font-size: 1.75rem;
    }
}

.forminator-subtitle {
    color: rgba($black, .5);
    font-size: .875rem;
    font-weight: 300;
    letter-spacing: 0;
    margin-bottom: 0;

    @include media-breakpoint-up(md) {
        font-size: 1rem;
    }
}

.forminator-custom-form:focus {
    border: none;
    border-radius: 0;
    box-shadow: none;
    outline: none;
}

.forminator-design--none {
    input,
    textarea {
        background-color: $input-bg-color;
        border: 1px solid $input-border-color;
        font-size: 75%;
        line-height: 1.5;
        transition: .25s ease-in-out;

        &::placeholder {
            color: rgba($body-color, .25);
        }

        &:focus {
            background-color: $input-bg-focus-color;
            border-color: $input-border-focus-color;
            box-shadow: inset 0 0 0 .25rem $primary;
            outline: 0;
        }

        &:hover {
            background-color: $input-bg-hover-color;
        }
    }
}

.forminator-required {
    color: $red;
    font-weight: 700;
    vertical-align: middle;
}

.forminator-has_error {
    .forminator-error-message {
        background-color: $input-bg-error-color;
        border: 1px solid $red;
        color: $red;
        display: block;
        font-size: .875rem;
        margin: .5rem 0;
        padding: .25rem 1rem;
    }

    input,
    textarea,
    .forminator-checkbox span[aria-hidden],
    .forminator-radio span[aria-hidden] {
        background-color: $input-bg-error-color;
        border-color: $red;
        transition: .25s ease-in-out;

        &:focus {
            background-color: $input-bg-error-focus-color;
            box-shadow: inset 0 0 0 .25rem $red;
            outline: 0;
        }

        &:hover {
            background-color: $input-bg-error-hover-color;
        }
    }
}

.forminator-label {
    display: block;
}

.forminator-description {
    color: rgba($body-color, .5);
    display: flex;
    font-size: 75%;
    font-weight: 300;
    margin-top: $paragraph-margin-bottom / 4;
}

.forminator-field[role="radiogroup"] .forminator-description {
    margin-top: $paragraph-margin-bottom / 2;
}

.forminator-checkbox input,
.forminator-file-upload input[type=file],
.forminator-radio input,
.forminator-select,
.ui-corner-all .ui-icon,
.forminator-screen-reader-only {
    border: 0!important;
    clip: rect(0 0 0 0)!important;
    clip-path: inset(50%)!important;
    height: 1px!important;
    margin: -1px!important;
    padding: 0!important;
    overflow: hidden!important;
    position: absolute!important;
    white-space: nowrap!important;
    width: 1px!important;
}

.forminator-checkbox,
.forminator-radio {
    display: flex;
    line-height: 1;
    margin: $paragraph-margin-bottom / 2 0;
    position: relative;

    span {
        display: block;
    }

    span[aria-hidden] {
        background-color: $input-radio-bg-color;
        border: 1px solid $input-border-color;
        flex: 0 0 1.25rem;
        height: 1.25rem;
        position: relative;
        width: 1.25rem;

        &:before {
            color: rgba($black, .75);
            content: "✓";
            display: block;
            left: 50%;
            opacity: 0;
            position: absolute;
            top: 50%;
            transition: .25s ease-in-out;
            transform: translate(-50%,-50%);
        }
    }

    input:checked + span[aria-hidden] {
        border-color: $input-selected-border-color;

        &:before {
            opacity: 1;
        }
    }

    span:not([aria-hidden]) {
        align-self: center;
        flex: 0 1 auto;
        line-height: 1.5;
        margin-left: 10px;
    }
}

label.forminator-checkbox,
label.forminator-radio {
    font-size: 87.5%;
}

.forminator-radio span[aria-hidden] {
    border-radius: 50%;

    &:before {
        background-color: rgba($black, .75);
        border-radius: 100%;
        content: " ";
        height: 10px;
        width: 10px;
    }
}

.forminator-checkbox-inline,
.forminator-radio-inline {
    display: inline-flex;
    margin-bottom: 0;
    margin-right: $paragraph-margin-bottom;
    vertical-align: middle;
}

.forminator-input,
.forminator-textarea {
    border: 0;
    border-radius: 0;
    display: block;
    height: auto;
    margin: 0;
    padding: $paragraph-margin-bottom / 4;
    width: 100%;
}

.forminator-phone {
    display: block;
    position: relative;
    width: 100%;

    &:not(.allow-dropdown) .forminator-input {
        padding-right: 51px;
    }

