.forminator-select-list {
    font-size: 75%;
    line-height: 1.5;
    position: relative;
}

.forminator-value-wrapper {
    background-color: $input-bg-color;
    border: 1px solid $input-border-color;
    position: relative;
}

.forminator-value {
    padding: $paragraph-margin-bottom / 4 40px $paragraph-margin-bottom / 4 $paragraph-margin-bottom / 4;
    transition: .25s ease-in-out;
}

.forminator-dropdown-handle {
    align-items: center;
    display: flex;
    height: 20px;
    justify-content: center;
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);

    .forminator-icon-chevron-down:before {
        content: "⌄";
    }

    [class*=forminator-icon-] {
        display: block;
        transition: .25s ease-in-out;

        &:before {
            display: block;
            font-family: 'forminator-icons-font';
            line-height: 1;
        }
    }
}

.forminator-is_open .forminator-dropdown-handle [class*=forminator-icon-] {
    transform: rotate(180deg)
}

.forminator-dropdown-list {
    border: 0;
    height: 0;
    list-style: none;
    margin: 0;
    padding: 0;
    max-height: 210px;
    overflow-x: hidden;
    overflow-y: auto;
    position: relative;
    transform: scale(0);
    transform-origin: right top;
    transition: .25s ease-in-out;

    li {
        cursor: pointer;
        display: block;
        padding: $paragraph-margin-bottom / 4;
        transition: .25s ease-in-out;

        &.current {
            background-color: $input-bg-focus-color;

            &:hover {
                background-color: $input-bg-focus-color;
            }
        }

        &:hover {
            background-color: $input-bg-hover-color;
        }
    }
}

.forminator-is_open .forminator-dropdown-list {
    border: 1px solid $input-border-color;
    border-top: 0;
    height: auto;
    transform: scale(1);
}

.forminator-textarea {
    font-size: .875rem;
    line-height: 1.25;
    min-height: 6rem;
}

.forminator-button {
    @extend .btn;
}

.forminator-input-with-icon {
    border-radius: 0;
    position: relative;

    [class*=forminator-icon-] {
        display: block;
        left: 10px;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        transition: .25s ease-in-out;
    }

    .forminator-input {
        padding-left: 36px;
    }
}

.forminator-calendar {
    &:before {
        background-color: $primary;
        bottom: 1px;
        box-shadow: $box-shadow;
        content: " ";
        display: block;
        left: 0;
        position: absolute;
        right: 0;
        top: 0;
    }
}

.ui-datepicker-header {
    display: block;
    padding: .875rem;
    position: relative;
    z-index: 1;
}

.ui-datepicker-header .ui-corner-all {
    align-items: center;
    border-bottom: 0;
    color: rgba($black, .875);
    display: flex;
    font-size: 1rem;
    height: 30px;
    justify-content: center;
    position: absolute;
    top: 11px;
    transition: .25s ease-in-out;
    width: 30px;

    &:hover {
        background-color: transparent;
        color: $black;
    }
}

.ui-datepicker-prev {
    left: 10px;
    right: unset;
    
    &:before {
        content: "❮";
        font-family: 'forminator-icons-font';
        line-height: 1;
    }
}

.ui-datepicker-next {
    left: unset;
    right: 10px;

    &:before {  
        content: "❯";
        font-family: 'forminator-icons-font';
        line-height: 1;
    }
}

.ui-datepicker-title {
    display: flex;
    padding: 0 40px;

    select {
        appearance: none;
        background-color: #dddddd;
        border: 1px solid #cccccc;
        border-radius: 0;
        font-size: .875rem;
        flex: 1;
        height: 1.5rem;
        letter-spacing: .05rem;
        line-height: 1.5rem;
        margin: 0 5px;
        padding: 0 10px;
        text-transform: uppercase;

        &:first-child {
            margin-left: 0;
        }

        &:last-child {
            margin-right: 0;
        }
    }
}

.ui-datepicker-calendar {
    font-size: .875rem;
    font-weight: 300;
    line-height: 1.5;
    margin: 0;
    padding: 0;
    position: relative;
    table-layout: fixed;
    width: auto;
    z-index: 1;

    tr {
        margin: 0;
        padding: 0;
    }

    th {
        padding: .5rem .75rem;
    }

    td {
        padding: 0 .25rem;
    }

    th,
    td {
        text-align: center;

        &:first-child {
            padding-left: 1rem;
        }

        &:last-child {
            padding-right: 1rem;
        }
    }

    td a {
        border-bottom: 0;
        color: rgba($black, .875);
        display: block;
        padding: .5rem;
        transition: .25s ease-in-out;

        &:hover {
            background-color: transparent;
            color: $black;
        }
    }
}

.forminator-ui.forminator-custom-form:not(.forminator-size--small)[data-grid=open] .forminator-timepicker .forminator-col-4 {
    flex: 0 0 11.11111%;

    .forminator-select-list:before {
        color: transparent;
        content: ".";
        display: block;
        font-size: 1rem;
        line-height: 1.75;
        margin: 0;

        @include media-breakpoint-up(md) {
            font-size: 1.25rem;
        }
    }
}

.forminator-file-upload {
    align-items: center;
    display: flex;

    .forminator-button-upload {
        background-color: $input-bg-color;
        border: 1px solid $input-border-color;
        box-shadow: $button-box-shadow-light;
        display: block;
        font-size: 1rem;
        height: auto;
        line-height: inherit;
        margin-right: 1rem;
        padding: $paragraph-margin-bottom / 4 $paragraph-margin-bottom;
        transition: .25s ease-in-out;

        &:hover {
            background-color: $input-bg-hover-color;
            box-shadow: $button-box-shadow;
        }
    }

    & > span {
        color: rgba($body-color, .5);
        display: block;
        flex: 0 1 auto;
        font-size: .875rem;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;

        &:not(:last-child) {
            margin-right: 1rem;
        }
    }
}

.forminator-button-delete {
    align-items: center;
    background-color: transparent;
    border: 0;
    border-radius: 100%;
    box-shadow: none;
    color: $red;
    display: flex;
    flex: 0 0 auto;
    height: 1rem;
    justify-content: center;
    margin: 0;
    padding: 0;
    width: 1rem;

    [class*=forminator-icon-] {
        display: block;
    }
}

.forminator-button-submit {
    @extend .btn-primary;
    @extend .btn-bold;
}

i.forminator-loading:before {
    display: none;
}

// Forminator Poll Colors
// Yellow: #fce409
// Blue: #4372b8
// Red: #bf282d
// Black: #000000
// Green: #31b523
// Purple: #4b0577
// Orange: #de570d

.forminator-ui.forminator-poll .forminator-poll-header {
    .forminator-question {
        font-size: 175%;
        font-weight: 700;
    }

    .forminator-description {
        display: block;
        font-size: 100%;
        font-weight: 300;
        margin: .5rem 0 $paragraph-margin-bottom / 2;
    }
}

.forminator-ui.forminator-poll .forminator-poll-footer {
    margin: $paragraph-margin-bottom 0;

    .forminator-button-back {
        @extend .btn-primary;
    }
}

.forminator-chart-wrapper {
    margin-bottom: $paragraph-margin-bottom;
}

.forminator-ui:not(.forminator-size--small) .forminator-chart-wrapper ul.forminator-chart-legend {
    display: block;
    margin: $paragraph-margin-bottom 0;

    li {
        font-size: 87.5%;
        line-height: 1.5;
        margin: 0 0 1rem;
        padding-left: 2rem;
        
        @include media-breakpoint-up(md) {
            max-width: 75%;
        }

        @include media-breakpoint-up(lg) {
            max-width: 50%;
        }

        .forminator-chart-legend--color {
            height: 1.25rem;
            top: 50%;
            transform: translateY(-50%);
            width: 1.25rem;
        }
    }
}

.forminator-ui.forminator-poll .forminator-chart-legend li strong {
    color: $body-color;
    font-weight: 400;
}

// Quizzes
.forminator-quiz {
    .forminator-legend {
        display: block;
        font-weight: 700;
        margin: $paragraph-margin-bottom / 2 0;
    }

    .forminator-question:focus {
        outline: none;
    }

    label {
        display: block;
        font-size: 87.5%;
        margin: .5rem 0;
        padding-left: .25rem;

        input[type=radio] {
            transform: scale(1.75);
        }

        .forminator-icon-check:before {
            color: $green;
        }

        .forminator-icon-cancel:before {
            color: $red;
        }
    }

    .forminator-answer--design {
        padding-left: .75rem;
    }

    .forminator-question--result {
        display: block;
        font-weight: 700;
        margin: $paragraph-margin-bottom / 2 0;

        &.forminator-show {
            @extend .alert-message;
        }
    }
}

[data-question-type=personality] {
    input[type=radio]:focus {
        box-shadow: none;
        outline: 2px $primary;
    }
}

.forminator-icon-check:before {
    display: none;
}

.forminator-quiz--result {
    margin: $paragraph-margin-bottom 0;
}

.forminator-result--info {
    display: flex;
}

.forminator-result--quiz-name {
    font-size: 1.25rem;
    font-weight: 700;
    line-height: 2;
    margin-right: 1rem;
}

.forminator-result--retake {
    @extend .btn-primary;
    @extend .btn-small;

    i:before {
        display: none;
    }
}

.forminator-result--content {
    align-items: center;
    background-color: $primary;
    box-shadow: $box-shadow;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin: $paragraph-margin-bottom 0;
    padding: 1rem;

    @include media-breakpoint-up(sm) {
        margin: $paragraph-margin-bottom -1rem;
    }

    @include media-breakpoint-up(lg) {
        flex-direction: row;
        justify-content: space-between;
    }
}

.forminator-result--text {
    flex: 0 0 100%;
    padding: 0 1rem;

    @include media-breakpoint-up(lg) {
        flex: 0 0 50%;
    }
}

.forminator-result--title {
    font-size: 2.5rem;
    font-weight: 700;
    line-height: 1;
    margin: $paragraph-margin-bottom 0 $paragraph-margin-bottom / 2;

    @include media-breakpoint-up(sm) {
        font-size: 3rem;
    }

    @include media-breakpoint-up(md) {
        font-size: 3.5rem;
    }

    @include media-breakpoint-up(lg) {
        font-size: 4rem;
    }

    @include media-breakpoint-up(xl) {
        font-size: 4.5rem;
    }

    &:before {
        content: "Your result";
        display: block;
        font-size: 1rem;
        font-weight: 300;
        letter-spacing: .125rem;
        line-height: 1;
        margin-bottom: 0;
        text-transform: uppercase;
    }
}

.forminator-result--image {
    border: 1rem solid $black;
    flex: 0 0 100%;

    @include media-breakpoint-up(lg) {
        flex: 0 0 50%;
    }
}

.forminator-response-message.forminator-show {
    @extend .alert-message;

    p:last-child {
        margin-bottom: 0;
    }

    &.forminator-success {
        p {
            margin-bottom: 0;
        }
    }
}

.StripeElement {
    background-color: $input-bg-color !important;
    border: 1px solid $input-border-color;
    color: $body-color !important;
    font-family: 'Avenir', sans-serif !important;
    font-size: calc(1.25rem * .75) !important;
    line-height: 1.5 !important;
    padding: 12px !important;
}

#captcha-1 {
    display: none;
}

.recaptcha {
    margin-bottom: 0 !important;
}